.accordion {
  border-top: 1px solid #333;
}

.accordion:last-child {
  border-bottom: 1px solid #333;
}

.accordion_title {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 15px;
  border: none;
  box-sizing: border-box;
  background-color: transparent;
  font-size: 16px;
  line-height: 20px;
  text-align: left;
  cursor: pointer;
}

.accordion_title .category {
  display: block;
  min-width: 80px;
  margin-right: 20px;
  padding: 5px 0;
  background-color: #eee;
  border-radius: 5px;
  font-size: 14px;
  text-align: center;
}

.accordion_title .text {
  display: block;
  flex-grow: 1;
  font-weight: 700;
  word-break: break-all;
}

.accordion_title .ico_arrow {
  width: 8px;
  height: 8px;
  margin-top: -2px;
  border: 2px solid #333;
  border-width: 2px 0 0 2px;
  transform: rotate(225deg);
}

.accordion_title.is_active .ico_arrow {
  margin-top: 2px;
  transform: rotate(45deg);
}

.accordion_title.is_active + .accordion_desc {
  display: block;
}

.accordion_desc {
  display: none;
  padding: 20px;
  border-top: 1px solid #333;
  word-break: break-all;
}
