.tab_list {
  display: flex;
  justify-content: center;
}

.tab_item {
  margin-left: 20px;
}

.tab_item:first-child {
  margin-left: 0;
}

.tab_menu {
  display: block;
  padding: 10px 32px;
  border: 1px solid #333;
  border-radius: 20px;
  background-color: transparent;
  font-size: 16px;
  line-height: 20px;
  cursor: pointer;
}
