.footer {
  position:relative;
  margin-top: auto;
  padding: 40px 40px 50px;
  background-color: #090119;
}

.footer .sns_list {
  position: absolute;
  top: 30px;
  right: 40px;
}

.footer .sns_item {
  margin-top: 10px;
}

.footer .sns_item:first-child {
  margin-top: 0;
}

.footer .sns_item .img_box {
  display: block;
  width: 30px;
  height: 30px;
}

.footer .sns_item img {
  display: block;
  width: 100%;
}

.footer .info_box .desc,
.footer .copyright {
  font-size: 13px;
  color: #807d81;
}

.footer .info_box .desc {
  margin-top: 11px;
}

.footer .info_box .desc:first-of-type {
  margin-top: 0;
}

.footer .copyright {
  display: block;
  margin-top: 40px;
  font-weight: 700;
  text-transform: uppercase;
}
